@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  color: #ffdead;
  font-family: 'Roboto Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  background: #6d678e; /* fallback for old browsers */
  background: linear-gradient(to bottom, #6d678e, #f6b5cc);

  /* height: 100vh; */
}

.bg-mine {
  background: #6d678e; /* fallback for old browsers */
  background: linear-gradient(100deg, #6d678e, #f6b5cc);
}

.bg-mine--dim {
  background-color: #7c888d;
}

.bg-mine-new {
  background-color: #fff;
}

.color-mine {
  color: #fff;
}

.no-border {
  border: none;
}

.border-mine {
  border: 1px solid #fff;
}

.width-mine {
  width: 20rem;
}

.no-outline {
  outline: none;
  outline-offset: 0px;
}

.no-outline:focus-visible {
  outline: none;
  outline-offset: 0px;
}

.outline-mine:focus-visible {
  outline: 2px solid #fff;
}

.outline-mine-onhover:hover {
  outline: 2px solid #fff;
}

.App {
  text-align: center;
}

.particles {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.no-border {
  border: none;
}

.myInput {
  font-family: 'Roboto Mono';
  color: #fff;
}

.myInput:-ms-input-placeholder {
  font-family: 'Roboto Mono';
  color: #fff;
}

.myInput::placeholder {
  font-family: 'Roboto Mono';
  color: #fff;
}

.bg-trans {
  background: transparent;
}

.icon {
  filter: brightness(0) invert(1);
}

.icon-move {
  top: 50%;
  left: 15px;
  transform: translate(0, -50%);
}

.button-move {
  top: 50%;
  left: 80%;
  transform: translate(0, -50%);
  color: rgb(0, 0, 0);
}

